body {
  text-align: center;
  padding-top: 50px;
}

select {
  width: 200px;
  height: 30px;
}

.loader {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border: 5px solid red;
  border-radius: 50%;
  margin: 10px auto auto;
  animation: pulse 1s linear infinite;
  display: block;
  position: relative;
}

.loader:after {
  content: "";
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border: 5px solid #ff2323;
  border-radius: 50%;
  animation: scaleUp 1s linear infinite;
  display: inline-block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@keyframes scaleUp {
  0% {
    transform: translate(-50%, -50%)scale(0);
  }

  60%, 100% {
    transform: translate(-50%, -50%)scale(1);
  }
}

@keyframes pulse {
  0%, 60%, 100% {
    transform: scale(1);
  }

  80% {
    transform: scale(1.2);
  }
}

img {
  max-width: 30%;
  max-height: 30%;
  width: auto;
  height: auto;
  -o-object-fit: contain;
  object-fit: contain;
}

.error {
  color: red;
}

.breed-select {
  text-align: center;
  font-size: 16px;
  display: inline-block;
}

.cat-info {
  justify-content: center;
  gap: 2rem;
  padding-top: 50px;
  display: flex;
}

.cat-info__description {
  text-align: left;
  max-width: 500px;
  flex-direction: column;
  justify-content: space-around;
  line-height: 1.5rem;
  display: flex;
}

.cat-name {
  text-transform: uppercase;
}

.cat-temperament {
  font-style: italic;
}

.cat-temperament__name {
  font-weight: 700;
}

.ss-main {
  background-color: #f0f0f0;
  border: 1px solid red;
  border-radius: 15px;
}

.ss-single-selected {
  color: red;
  font-weight: 100;
}

.hidden {
  display: none;
}

